@import url("https://fonts.googleapis.com/css2?family=Lobster&family=Poppins:wght@400;700&display=swap");
// Colours
$primary: #9730c8;
$secondary: #5fbab0;
$dark: #1e1e1e;

// Size
$mainHeading: max(3vw, 32px);
$subHeading: 1rem;
$button: 1.3rem;

// fonts
$lobster: "Lobster", sans-serif;
$poppins: "Poppins", monospace;

// transition
$fast: 0.3s ease-in-out;
$medium: 0.6s ease-in-out;
$slow: 0.9s ease-in-out;

// Media Querys
$tablet: 0.9rem;
$mobile: 0.8rem;
